<template>
    <div class="wrapper">


        <swiper :options="swiperOption">
        <!-- slides -->
        <swiper-slide v-for="item in swiperList" :key="item.id"><img class="swiper-img" :src="item.imgUrl"/></swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
  
</template>
<script>
export default {
    name:'HomeSwiper',
    data(){
        return{
           swiperOption:{
               pagination:'.swiper-pagination',
               loop:true
           },
           swiperList:[{
               id:'0001',
               imgUrl:'http://img1.qunarzz.com/piao/fusion/1811/7c/8e5c4ab8ee8b7402.jpg_750x200_dd7a38dd.jpg'
           },{
               id:'0002',
               imgUrl:'http://img1.qunarzz.com/piao/fusion/1809/12/856f100069809e02.jpg_750x200_e3485a2b.jpg'
           },{
               id:'0003',
               imgUrl:'http://img1.qunarzz.com/piao/fusion/1811/39/6b1f0402bb1d5902.jpg_750x200_3c7661d6.jpg'
           },{
               id:'0004',
               imgUrl:'http://img1.qunarzz.com/piao/fusion/1812/a8/90d66a621daf6802.jpg_750x200_d479b789.jpg'
           },{
               id:'0005',
               imgUrl:'http://img1.qunarzz.com/piao/fusion/1811/f7/72ce947f4dff4c02.jpg_750x200_aa583dba.jpg'
           },{
               id:'0006',
               imgUrl:'http://img1.qunarzz.com/piao/fusion/1811/a8/cb43c4ac6c215d02.jpg_750x200_83dee994.jpg'
           },{
               id:'0007',
               imgUrl:'http://img1.qunarzz.com/piao/fusion/1709/39/b98fdb3ee7478e02.jpg_750x200_be64b756.jpg'
           }
           ] 
        }
    }
}
</script>
<style lang="stylus" scoped>
    .wrapper >>> .swiper-pagination-bullet-active
        background :white
    .wrapper
        width:100%
        height: 0
        overflow :hidden
        padding-bottom :26.67%
        .swiper-img
             width:100%
</style>



