<template>
<div class="header">
    <div class="header-left">
        <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
        <span class="iconfont">&#xe632;</span>
        输入城市/景点/游玩主题
    </div>
    <div class="header-right">
        三亚
        <span class="iconfont arrow-icon">&#xe64a;</span>
    </div>
</div>    
</template>
<script>
export default {
    name:"HomeHeader"
}
</script>
<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl';
    .header
        height : 0.86rem
        line-height :0.86rem
        display :flex
        background :$bgColor
        color :white
        .header-left
            float :left
            width:0.64rem
            .back-icon
                text-align: center
                font-size: .4rem
        .header-input
            flex :1
            height :0.62rem
            line-height :0.62rem
            background :white
            color :#ccc
            border-radius:.1rem 
            margin-top: .12rem
            margin-left : .2rem
            text-align :left
            padding-left :.2rem
        .header-right
            float :right
            width: 1.24rem
            text-align :center  
            .arrow-icon
                font-size: .24rem
                margin-left -.04rem 
</style>



