<template>
    <div class="icons">
        <div class="icon">
            <div class="icon-img">
                <img class="icon-imgcontent" src="http://img1.qunarzz.com/piao/fusion/1803/95/f3dd6c383aeb3b02.png"/>
            </div>
            
            <p class="icon-desc">景点门票</p>
        </div>
    </div>
</template>
<script>
export default {
    name:'HomeIcons'
}
</script>
<style lang="stylus" scoped>
    .icons
        height :0
        padding-bottom :50%
        overflow :hidden
        .icon
            position: relative
            overflow :hidden
            width :25%
            height :0
            padding-bottom :25%
            float :left
            .icon-img
                position :absolute
                top:0
                left :0
                right:0
                bottom :.44rem
                box-sizing :border-box
                padding:.1rem
                .icon-imgcontent
                    display :block
                    margin:0 auto
                    height :100%
            .icon-desc
                position :absolute
                left :0
                right:0
                bottom :0
                height :.44rem
                line-height :.44rem
                color :#333       
                

</style>



